import React from "react";

const FuturisticStatusbarStyles = () => (
  <style>
    {`
      /* FUTURISTIC STATUS BAR V2 STYLES */

.futuristic-statusbar-v2 {
  position: fixed;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 95%;

  z-index: 1999;
  font-family: "Orbitron", "Segoe UI", sans-serif;

  /* Glassmorphism & Base Style */
  background: rgba(15, 15, 25, 0.5);
  backdrop-filter: blur(16px) saturate(200%);
  color: var(--text-color, #e0e0e0);
  border: 1px solid var(--border-color, rgba(138, 43, 226, 0.4));
  border-radius: 12px;
  overflow: hidden;

  /* Boot-up Animation */
  opacity: 0;
  transform: translateX(-50%) translateY(20px);
  transition: opacity 0.8s ease-out, transform 0.8s ease-out;
}

.futuristic-statusbar-v2.is-booted {
  opacity: 1;
  transform: translateX(-50%) translateY(0);
}

/* Animated Grid Background */
.grid-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(
      var(--glow-color, #00ffff) 1px,
      transparent 1px
    ),
    linear-gradient(to right, var(--glow-color, #00ffff) 1px, transparent 1px);
  background-size: 40px 40px;
  opacity: 0.05;
  animation: move-grid 20s linear infinite;
  z-index: 1;
}

@keyframes move-grid {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 40px 40px;
  }
}

/* Main Content Wrapper */
.statusbar-content {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 25px;
  z-index: 2;
}

/* Decorative Corner Brackets */
.corner-bracket {
  position: absolute;
  width: 20px;
  height: 20px;
  border-style: solid;
  border-color: var(--glow-color, #00ffff);
  opacity: 0;
  transition: opacity 1s ease 0.5s; /* Fade in after boot */
}
.futuristic-statusbar-v2.is-booted .corner-bracket {
  opacity: 0.5;
}
.corner-bracket.top-left {
  top: -2px;
  left: -2px;
  border-width: 2px 0 0 2px;
  border-radius: 8px 0 0 0;
}
.corner-bracket.top-right {
  top: -2px;
  right: -2px;
  border-width: 2px 2px 0 0;
  border-radius: 0 8px 0 0;
}
.corner-bracket.bottom-left {
  bottom: -2px;
  left: -2px;
  border-width: 0 0 2px 2px;
  border-radius: 0 0 0 8px;
}
.corner-bracket.bottom-right {
  bottom: -2px;
  right: -2px;
  border-width: 0 2px 2px 0;
  border-radius: 0 0 8px 0;
}

/* Data Readout Styling */
.status-group-v2 {
  display: flex;
  align-items: center;
  gap: 24px;
}

.data-readout {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #aab5c3;
}

.data-icon {
  color: var(--glow-color, #00ffff);
  filter: drop-shadow(0 0 3px var(--glow-color, #00ffff));
}

.data-label {
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 1px;
  opacity: 0.6;
}

.data-value {
  font-family: "Roboto Mono", monospace;
  font-size: 16px;
  font-weight: 700;
  color: white;
}

/* Action Button Styling */
.action-button {
  background: transparent;
  border: 1px solid transparent;
  color: #aab5c3;
  width: 36px;
  height: 36px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.action-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  border: 1px solid var(--border-color, rgba(138, 43, 226, 0.4));
  transition: all 0.3s ease;
}

.action-button:hover {
  color: white;
}

.action-button:hover::before {
  transform: scale(1.2);
  opacity: 0;
}

.action-button:active {
  transform: scale(0.9);
}

      `}
  </style>
);
export default FuturisticStatusbarStyles;
