import { useRef } from "react";
import PropTypes from "prop-types";
import {
  Bold,
  Italic,
  Underline,
  Strikethrough,
  Code,
  Link,
  Image,
  List,
  ListOrdered,
  Quote,
  AlignLeft,
  AlignCenter,
  AlignRight,
  Indent,
  Outdent,
  Table,
  Pilcrow,
  Columns,
  Eraser,
  Undo,
  Redo,
  Minus,
  Type,
  ArrowDownUp,
  FileUp,
  Superscript,
  Subscript,
  Video,
  Sparkles,
} from "lucide-react";
import ModernToolbarButton from "./components/ModernToolbarButton";
import ModernDropdown from "./components/ModernDropdown";
import ModernColorPicker from "./components/ModernColorPicker";
import ModernEmojiPicker from "./components/ModernEmojiPicker";
import ModernToolBarStyle from "../../../../styles/theme/modern/ModernToolBar.style";

const FONT_FACES = [
  "Arial",
  "Georgia",
  "Helvetica",
  "Tahoma",
  "Times New Roman",
  "Verdana",
  "Courier New",
];
const FONT_SIZES = { Small: "2", Normal: "3", Large: "5", Huge: "7" };
const HEADINGS = {
  "Heading 1": "<h1>",
  "Heading 2": "<h2>",
  "Heading 3": "<h3>",
  "Heading 4": "<h4>",
  "Heading 5": "<h5>",
  Paragraph: "<p>",
};
const LINE_HEIGHTS = {
  None: "0.5",
  Single: "1",
  1.15: "1.15",
  1.5: "1.5",
  Double: "2",
};

const columnTableStyle =
  "width: 100%; table-layout: fixed; border-collapse: separate; border-spacing: 1rem; position: relative; margin: 1rem 0;";
const columnCellStyle = "padding: 1rem; vertical-align: top;";
const COLUMN_LAYOUTS = {
  "2 Columns (1:1)": `
    <div style="position: relative;">
      
      <table style="${columnTableStyle}">
        <tbody>
          <tr>
            <td style="${columnCellStyle} width: 50%;"><p>Column 1</p></td>
            <td style="${columnCellStyle} width: 50%;"><p>Column 2</p></td>
          </tr>
        </tbody>
      </table>
    </div>`,

  "3 Columns (1:1:1)": `
    <div style="position: relative;">
      
      <table style="${columnTableStyle}">
        <tbody>
          <tr>
            <td style="${columnCellStyle} width: 33.33%;"><p>C1</p></td>
            <td style="${columnCellStyle} width: 33.33%;"><p>C2</p></td>
            <td style="${columnCellStyle} width: 33.33%;"><p>C3</p></td>
          </tr>
        </tbody>
      </table>
    </div>`,

  "2 Columns (1:2)": `
    <div style="position: relative;">
      
      <table style="${columnTableStyle}">
        <tbody>
          <tr>
            <td style="${columnCellStyle} width: 33.33%;"><p>C1</p></td>
            <td style="${columnCellStyle} width: 66.66%;"><p>C2</p></td>
          </tr>
        </tbody>
      </table>
    </div>`,

  "2 Columns (2:1)": `
    <div style="position: relative;">
      
      <table style="${columnTableStyle}">
        <tbody>
          <tr>
            <td style="${columnCellStyle} width: 66.66%;"><p>C1</p></td>
            <td style="${columnCellStyle} width: 33.33%;"><p>C2</p></td>
          </tr>
        </tbody>
      </table>
    </div>`,
};

const ModernToolbar = ({
  applyStyle,
  applyLineHeight,
  activeStyles,
  onOpenLinkModal,
  onImageUpload,
  onDocxUpload,
  onVideoModalOpen,
  onTableModalOpen,
  onAiRefactor,
  content,
  isGenerating,
  isAiLoadingMinimized,
}) => {
  const imageFileInputRef = useRef(null);
  const docxFileInputRef = useRef(null);

  return (
    <>
      <ModernToolBarStyle />

      <div className="modern-toolbar">
        {/* Undo/Redo */}
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Undo (Ctrl+Z)"
            onClick={() => applyStyle("undo")}
          >
            <Undo size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Redo (Ctrl+Y)"
            onClick={() => applyStyle("redo")}
          >
            <Redo size={18} />
          </ModernToolbarButton>
        </div>

        <div className="toolbar-separator" />

        {/* Font & Text Style Dropdowns */}
        <div className="toolbar-group">
          <ModernDropdown title="Headings" icon={<Pilcrow size={26} />}>
            {Object.entries(HEADINGS).map(([key, value]) => (
              <button
                key={key}
                className="modern-dropdown-item"
                onClick={() => applyStyle("formatBlock", value)}
              >
                {key}
              </button>
            ))}
          </ModernDropdown>
          <ModernDropdown title="Font Family" icon={<Type size={26} />}>
            {FONT_FACES.map((face) => (
              <button
                key={face}
                className="modern-dropdown-item"
                style={{ fontFamily: face }}
                onClick={() => applyStyle("fontName", face)}
              >
                {face}
              </button>
            ))}
          </ModernDropdown>

          <ModernDropdown title="Font Size" icon={"F"}>
            {Object.entries(FONT_SIZES).map(([key, value]) => (
              <button
                key={key}
                className="modern-dropdown-item"
                onClick={() => applyStyle("fontSize", value)}
              >
                {key}
              </button>
            ))}
          </ModernDropdown>

          <ModernDropdown title="Line Height" icon={<ArrowDownUp size={26} />}>
            {Object.entries(LINE_HEIGHTS).map(([key, value]) => (
              <button
                key={key}
                className="modern-dropdown-item"
                onClick={() => applyLineHeight(value)}
              >
                {key}
              </button>
            ))}
          </ModernDropdown>
        </div>

        <div className="toolbar-separator" />

        {/* Basic Formatting */}
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Bold (Ctrl+B)"
            command="bold"
            activeStyles={activeStyles}
            onClick={() => applyStyle("bold")}
          >
            <Bold size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Italic (Ctrl+I)"
            command="italic"
            activeStyles={activeStyles}
            onClick={() => applyStyle("italic")}
          >
            <Italic size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Underline (Ctrl+U)"
            command="underline"
            activeStyles={activeStyles}
            onClick={() => applyStyle("underline")}
          >
            <Underline size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Strikethrough"
            command="strikeThrough"
            activeStyles={activeStyles}
            onClick={() => applyStyle("strikeThrough")}
          >
            <Strikethrough size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Superscript"
            command="superscript"
            activeStyles={activeStyles}
            onClick={() => applyStyle("superscript")}
          >
            <Superscript size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Subscript"
            command="subscript"
            activeStyles={activeStyles}
            onClick={() => applyStyle("subscript")}
          >
            <Subscript size={18} />
          </ModernToolbarButton>
          <ModernEmojiPicker applyStyle={applyStyle} />
        </div>

        <div className="toolbar-separator" />

        {/* Color Pickers */}
        <div className="toolbar-group">
          <ModernColorPicker command="foreColor" applyStyle={applyStyle} />
          <ModernColorPicker command="backColor" applyStyle={applyStyle} />
        </div>

        <div className="toolbar-separator" />

        {/* Alignment */}
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Align Left"
            command="justifyLeft"
            activeStyles={activeStyles}
            onClick={() => applyStyle("justifyLeft")}
          >
            <AlignLeft size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Align Center"
            command="justifyCenter"
            activeStyles={activeStyles}
            onClick={() => applyStyle("justifyCenter")}
          >
            <AlignCenter size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Align Right"
            command="justifyRight"
            activeStyles={activeStyles}
            onClick={() => applyStyle("justifyRight")}
          >
            <AlignRight size={18} />
          </ModernToolbarButton>
        </div>

        <div className="toolbar-separator" />

        {/* Lists & Indentation */}
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Ordered List"
            command="insertOrderedList"
            activeStyles={activeStyles}
            onClick={() => applyStyle("insertOrderedList")}
          >
            <ListOrdered size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Unordered List"
            command="insertUnorderedList"
            activeStyles={activeStyles}
            onClick={() => applyStyle("insertUnorderedList")}
          >
            <List size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Outdent"
            onClick={() => applyStyle("outdent")}
          >
            <Outdent size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Indent"
            onClick={() => applyStyle("indent")}
          >
            <Indent size={18} />
          </ModernToolbarButton>
        </div>

        <div className="toolbar-separator" />

        {/* Insert Elements */}
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Blockquote"
            onClick={() => applyStyle("formatBlock", "<blockquote>")}
          >
            <Quote size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Code Block"
            onClick={() => applyStyle("formatBlock", "<pre>")}
          >
            <Code size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Add/Edit Link (Ctrl+K)"
            onClick={onOpenLinkModal}
          >
            <Link size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Horizontal Line"
            onClick={() => applyStyle("insertHorizontalRule")}
          >
            <Minus size={18} />
          </ModernToolbarButton>
        </div>

        <div className="toolbar-separator" />

        {/* Media & Advanced */}
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Insert Image"
            onClick={() => imageFileInputRef.current?.click()}
          >
            <Image size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton title="Insert Video" onClick={onVideoModalOpen}>
            <Video size={20} />
          </ModernToolbarButton>
          <ModernToolbarButton title="Insert Table" onClick={onTableModalOpen}>
            <Table size={18} />
          </ModernToolbarButton>
          <ModernDropdown title="Insert Columns" icon={<Columns size={26} />}>
            {Object.entries(COLUMN_LAYOUTS).map(([key, value]) => (
              <button
                key={key}
                className="modern-dropdown-item"
                onClick={() => applyStyle("insertHTML", value + "<p><br></p>")}
              >
                {key}
              </button>
            ))}
          </ModernDropdown>
          <ModernToolbarButton
            title="Import from Word (.docx)"
            onClick={() => docxFileInputRef.current?.click()}
          >
            <FileUp size={18} />
          </ModernToolbarButton>
        </div>

        <div className="toolbar-separator" />

        {/* AI Feature & Clear Formatting */}
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Clear Formatting"
            onClick={() => applyStyle("removeFormat")}
          >
            <Eraser size={18} style={{ color: "red" }} />
          </ModernToolbarButton>

          <ModernToolbarButton
            title="Improve with AI"
            onClick={onAiRefactor}
            disabled={!content || content === "" || isGenerating}
            className="modern-ai-button mx-0 rounded-3"
          >
            {isGenerating && !isAiLoadingMinimized ? (
              <div className="spinner"></div>
            ) : (
              <Sparkles size={20} />
            )}
          </ModernToolbarButton>
        </div>
      </div>
      {/* Hidden file inputs */}
      <input
        type="file"
        accept="image/*"
        ref={imageFileInputRef}
        onChange={onImageUpload}
        style={{ display: "none" }}
      />
      <input
        type="file"
        accept=".docx"
        ref={docxFileInputRef}
        onChange={onDocxUpload}
        style={{ display: "none" }}
      />
    </>
  );
};

ModernToolbar.propTypes = {
  applyStyle: PropTypes.func.isRequired,
  applyLineHeight: PropTypes.func.isRequired,
  activeStyles: PropTypes.object.isRequired,
  onOpenLinkModal: PropTypes.func.isRequired,
  onImageUpload: PropTypes.func.isRequired,
  onDocxUpload: PropTypes.func.isRequired,
  onVideoModalOpen: PropTypes.func.isRequired,
  onTableModalOpen: PropTypes.func.isRequired,
  onAiRefactor: PropTypes.func.isRequired,
  content: PropTypes.string,
  isGenerating: PropTypes.bool.isRequired,
  isAiLoadingMinimized: PropTypes.bool.isRequired,
};

export default ModernToolbar;
