import { useState, useRef, useCallback, useEffect } from "react";
import PropTypes from "prop-types";
import mammoth from "mammoth";
import {
  diff_match_patch,
  DIFF_DELETE,
  DIFF_INSERT,
  DIFF_EQUAL,
} from "diff-match-patch";
import {
  AlignCenterVertical,
  AlignEndVertical,
  AlignStartVertical,
  Trash2,
} from "lucide-react";

// Component Imports (as requested, none are removed)
import Toolbar from "./components/Toolbar/Toolbar";
import Editor from "./components/Editor";
import LinkModal from "./modal/LinkModal";
import VideoModal from "./modal/VideoModal";
import TableModal from "./modal/TableModal";
import AiLoadingPopup from "./ai/AiLoadingPopup";
import AiSuggestionModal from "./ai/AiSuggestionModal";
import StatusBar from "./components/statusBar/StatusBar";
import { injectBootstrap } from "./bootstrap-injector.js";
import FormaTextStyles  from "./FormaText.styles.jsx"


/**
 * @typedef {Object} FormaTextProps
 * @property {string} [content]
 * @property {(value: string) => void} [setContent]
 * @property {string} [geminiApiKey]
 * @property {string} [parentEditorClass]
 * @property {string} [toolBarClass]
 * @property {string} [textContentClass]
 * @property {string} [theme]
 */

/**
 * The main parent component for the HTML Editor.
 * It manages state and orchestrates all the child components.
 *
 * @param {FormaTextProps} props
 * @returns {JSX.Element}
 */
export default function FormaText({
  content = "",
  setContent = () => {},
  geminiApiKey = "",
  parentEditorClass = "",
  toolBarClass = "",
  textContentClass = "",
  theme = "classic",
}) {
  // --- STATE MANAGEMENT ---
  const [editorId] = useState(
    `forma-text-editor-${Math.random().toString(36).substring(2, 9)}`
  );

  const editorRef = useRef(null);
  const savedRange = useRef(null);
  const isInternalUpdate = useRef(false);

  const [activeStyles, setActiveStyles] = useState(new Set());
  const [wordCount, setWordCount] = useState({ words: 0, chars: 0 });
  const [showSource, setShowSource] = useState(false);
  const [isFullScreen, setIsFullScreen] = useState(false);

  // Modal States
  const [isLinkModalOpen, setIsLinkModalOpen] = useState(false);
  const [linkModalData, setLinkModalData] = useState({ text: "", href: "" });
  const [editingLink, setEditingLink] = useState(null);
  const [isVideoModalOpen, setIsVideoModalOpen] = useState(false);
  const [isTableModalOpen, setIsTableModalOpen] = useState(false);

  // AI States
  const [isAiModalOpen, setIsAiModalOpen] = useState(false);
  const [aiSuggestion, setAiSuggestion] = useState("");
  const [highlightedDiff, setHighlightedDiff] = useState("");
  const [isGenerating, setIsGenerating] = useState(false);
  const [aiLoadingStatus, setAiLoadingStatus] = useState("");
  const [isAiLoadingMinimized, setIsAiLoadingMinimized] = useState(false);

  // Element Selection States
  const [selectedImage, setSelectedImage] = useState(null);
  const [selectedTable, setSelectedTable] = useState(null);
  const [selectionCoords, setSelectionCoords] = useState(null); // For positioning toolbars

  // --- CORE EDITOR LOGIC ---

  // Injects Bootstrap scoped to this editor instance
  useEffect(() => {
    injectBootstrap(editorId);
  }, [editorId]);

  // Update content state from editor's innerHTML
  const updateContent = useCallback(() => {
    if (editorRef.current) {
      isInternalUpdate.current = true;
      setContent(editorRef.current.innerHTML);
    }
  }, [setContent]);

  // Save and restore user selection (caret position)
  const saveSelection = useCallback(() => {
    const selection = window.getSelection();
    if (selection.rangeCount > 0) {
      const range = selection.getRangeAt(0);
      if (editorRef.current && editorRef.current.contains(range.commonAncestorContainer)) {
        savedRange.current = range;
      }
    }
  }, []);

  const restoreSelection = useCallback(() => {
    if (savedRange.current) {
      const selection = window.getSelection();
      selection.removeAllRanges();
      selection.addRange(savedRange.current);
    }
  }, []);

  // Sync external `content` prop with the editor
  useEffect(() => {
    if (isInternalUpdate.current) {
      isInternalUpdate.current = false;
      return;
    }
    if (editorRef.current && !showSource && content !== editorRef.current.innerHTML) {
      editorRef.current.innerHTML = content || "";
    }
  }, [content, showSource]);

  // Handle content changes from both editor and source view
  const handleEditorContentChange = useCallback((e) => {
    const newContent = showSource ? e.target.value : e.currentTarget.innerHTML;
    isInternalUpdate.current = true;
    setContent(newContent);
  }, [showSource, setContent]);

  // Update word and character count
  useEffect(() => {
    const tempDiv = document.createElement("div");
    tempDiv.innerHTML = content || "";
    const text = tempDiv.textContent || "";
    const words = text.trim().split(/\s+/).filter(Boolean).length;
    setWordCount({ words, chars: text.length });
  }, [content]);

  // --- TOOLBAR AND STYLE LOGIC ---

  // Update toolbar button states based on selection
  const updateToolbarState = useCallback(() => {
    const newActiveStyles = new Set();
    const commands = [
      "bold", "italic", "underline", "strikeThrough", "justifyLeft",
      "justifyCenter", "justifyRight", "insertOrderedList",
      "insertUnorderedList", "superscript", "subscript",
    ];
    commands.forEach((command) => {
      if (document.queryCommandState(command)) {
        newActiveStyles.add(command);
      }
    });
    setActiveStyles(newActiveStyles);
  }, []);

  // Apply a style command (e.g., bold, italic)
  const applyStyle = useCallback((style, value = null) => {
    editorRef.current?.focus();
    restoreSelection();
    document.execCommand(style, false, value);
    updateContent();
    updateToolbarState();
  }, [restoreSelection, updateContent, updateToolbarState]);

  // Apply line height to selected block elements
  const applyLineHeight = useCallback((lineHeight) => {
    editorRef.current?.focus();
    restoreSelection();
    const selection = window.getSelection();
    if (!selection.rangeCount) return;

    const getContainingBlock = (node) => {
      let element = node.nodeType === Node.ELEMENT_NODE ? node : node.parentNode;
      while (element && element !== editorRef.current) {
        if (window.getComputedStyle(element).display.match(/block|table|list-item/)) {
          return element;
        }
        element = element.parentNode;
      }
      return null;
    };

    const range = selection.getRangeAt(0);
    const startBlock = getContainingBlock(range.startContainer);
    const endBlock = getContainingBlock(range.endContainer);

    if (startBlock) {
      let currentBlock = startBlock;
      const blocksToModify = [];
      while (currentBlock) {
        blocksToModify.push(currentBlock);
        if (currentBlock === endBlock) break;
        currentBlock = currentBlock.nextElementSibling;
      }
      blocksToModify.forEach(block => {
        block.style.lineHeight = lineHeight;
      });
    }
    updateContent();
  }, [restoreSelection, updateContent]);

  // --- EVENT LISTENERS & HANDLERS ---

  // Listen for selection changes to update toolbar
  useEffect(() => {
    const handleSelectionChange = () => {
      const selection = window.getSelection();
      if (selection.rangeCount > 0) {
        const range = selection.getRangeAt(0);
        if (editorRef.current && editorRef.current.contains(range.commonAncestorContainer)) {
          saveSelection();
          updateToolbarState();
        }
      }
    };
    document.addEventListener("selectionchange", handleSelectionChange);
    return () => document.removeEventListener("selectionchange", handleSelectionChange);
  }, [saveSelection, updateToolbarState]);
  
  // Unified click handler to detect image/table selection
  const handleEditorClick = useCallback((e) => {
    const target = e.target;
    const img = target.closest('img');
    const table = target.closest('table.forma-text-table');

    if (img && editorRef.current.contains(img)) {
        setSelectedTable(null);
        setSelectedImage(img);
        const rect = img.getBoundingClientRect();
        const editorRect = editorRef.current.getBoundingClientRect();
        setSelectionCoords({
            top: rect.top - editorRect.top + editorRef.current.scrollTop,
            left: rect.left - editorRect.left + editorRef.current.scrollLeft,
            width: rect.width,
            height: rect.height,
        });
    } else if (table && editorRef.current.contains(table)) {
        setSelectedImage(null);
        setSelectedTable(table);
        const rect = table.getBoundingClientRect();
        const editorRect = editorRef.current.getBoundingClientRect();
        setSelectionCoords({
            top: rect.top - editorRect.top + editorRef.current.scrollTop,
            left: rect.left - editorRect.left + editorRef.current.scrollLeft,
            width: rect.width,
            height: rect.height,
        });
    } else {
        setSelectedImage(null);
        setSelectedTable(null);
        setSelectionCoords(null);
    }
  }, []);
  
  // Deselect elements when editor loses focus
  const handleEditorBlur = useCallback(() => {
      // Use a small timeout to allow context menu clicks to register
      setTimeout(() => {
        if (document.activeElement !== editorRef.current && !editorRef.current?.contains(document.activeElement)) {
            setSelectedImage(null);
            setSelectedTable(null);
            setSelectionCoords(null);
        }
      }, 100);
  }, []);

  const handleOpenLinkModal = useCallback(() => {
    saveSelection();
    const selection = window.getSelection();
    const parentElement = selection.anchorNode?.parentElement.closest("a");
    if (parentElement?.tagName === "A") {
      setLinkModalData({ text: parentElement.textContent, href: parentElement.href });
      setEditingLink(parentElement);
    } else {
      setLinkModalData({ text: selection.toString(), href: "" });
      setEditingLink(null);
    }
    setIsLinkModalOpen(true);
  }, [saveSelection]);

  // Keyboard shortcuts
  const handleKeyDown = useCallback((e) => {
    if (e.ctrlKey || e.metaKey) {
      const keyMap = {
        b: "bold", i: "italic", u: "underline",
        k: () => handleOpenLinkModal(),
        z: "undo", y: "redo",
      };
      if (keyMap[e.key]) {
        e.preventDefault();
        typeof keyMap[e.key] === "function" ? keyMap[e.key]() : applyStyle(keyMap[e.key]);
      }
    }
  }, [applyStyle, handleOpenLinkModal]);

  // --- MODAL & FILE HANDLERS ---

  const handleApplyLink = useCallback((data) => {
    setIsLinkModalOpen(false);
    restoreSelection();
    editorRef.current.focus();
    if (editingLink) {
      editingLink.href = data.href;
      editingLink.textContent = data.text;
    } else {
      const link = `<a href="${data.href}" target="_blank" rel="noopener noreferrer">${data.text}</a>`;
      applyStyle("insertHTML", link);
    }
    setEditingLink(null);
    updateContent();
  }, [editingLink, restoreSelection, applyStyle, updateContent]);

  const handleImageUpload = useCallback((event) => {
    const file = event.target.files[0];
    if (!file) return;
    const reader = new FileReader();
    reader.onload = (e) => {
      const imgHtml = `<span class="forma-text-image-wrapper" contenteditable="false"><img src="${e.target.result}" style="width: 250px; height: auto;" alt=""/></span>`;
      applyStyle("insertHTML", imgHtml);
    };
    reader.readAsDataURL(file);
    event.target.value = "";
  }, [applyStyle]);

  const handleDocxUpload = useCallback((event) => {
    const file = event.target.files[0];
    if (!file) return;
    const reader = new FileReader();
    reader.onload = (e) => {
      mammoth.convertToHtml({ arrayBuffer: e.target.result })
        .then((result) => setContent(result.value))
        .catch((error) => console.error("Error converting .docx to HTML:", error));
    };
    reader.readAsArrayBuffer(file);
    event.target.value = "";
  }, [setContent]);

  const handleInsertVideo = useCallback((videoUrl) => {
    setIsVideoModalOpen(false);
    if (!videoUrl) return;
    const getEmbedUrl = (url) => {
        let videoId;
        if (url.includes("youtube.com/watch?v=")) videoId = url.split("v=")[1].split("&")[0];
        else if (url.includes("youtu.be/")) videoId = url.split("youtu.be/")[1].split("?")[0];
        else if (url.includes("vimeo.com/")) videoId = url.split("vimeo.com/")[1].split("?")[0];
        if (url.includes("youtube") || url.includes("youtu.be")) return `https://www.youtube.com/embed/${videoId}`;
        if (url.includes("vimeo")) return `https://player.vimeo.com/video/${videoId}`;
        return null;
    };
    const embedUrl = getEmbedUrl(videoUrl);
    if (!embedUrl) {
      alert("Invalid video URL. Please use a valid YouTube or Vimeo link.");
      return;
    }
    restoreSelection();
    editorRef.current.focus();
    const videoHtml = `<div class="forma-text-video-wrapper" contenteditable="false"><iframe src="${embedUrl}" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe></div><p><br></p>`;
    applyStyle("insertHTML", videoHtml);
    updateContent();
  }, [restoreSelection, applyStyle, updateContent]);

  const handleCreateTable = useCallback((tableData) => {
    setIsTableModalOpen(false);
    let tableHTML =         '<table class="forma-text-table" style="width:90%; border-collapse: collapse; border: 1px solid #a0a0a0ff; margin: auto;"><tbody>';
    for (let i = 0; i < tableData.rows; i++) {
      tableHTML += "<tr>";
      for (let j = 0; j < tableData.cols; j++) {
        tableHTML +=  '<td style="border: 1px solid #afafafff; padding: 0.75rem;"><br></td>';
      }
      tableHTML += "</tr>";
    }
    tableHTML += "</tbody></table><p><br></p>";
    applyStyle("insertHTML", tableHTML);
  }, [applyStyle]);

  // --- IMAGE & TABLE MANIPULATION ---

  const alignImage = useCallback((alignment) => {
    const wrapper = selectedImage?.parentElement;
    if (!wrapper || !wrapper.classList.contains('forma-text-image-wrapper')) return;

    // Find the nearest block-level parent to apply text-align for centering
    let blockParent = wrapper.parentElement;
    while (blockParent && blockParent !== editorRef.current) {
        const display = window.getComputedStyle(blockParent).display;
        if (display === 'block' || display === 'flex' || display === 'grid') {
            break;
        }
        blockParent = blockParent.parentElement;
    }
    
    // Reset styles first
    wrapper.style.float = '';
    wrapper.style.display = 'inline-block'; // Default display
    wrapper.style.marginLeft = '';
    wrapper.style.marginRight = '';
    if(blockParent) blockParent.style.textAlign = ''; // Reset parent alignment

    switch (alignment) {
      case "center":
        // To center, we make the wrapper a block and use auto margins.
        // Or better, use textAlign on the parent block.
        if (blockParent) {
            blockParent.style.textAlign = 'center';
        } else {
            // Fallback if no suitable parent is found
            wrapper.style.display = 'block';
            wrapper.style.marginLeft = 'auto';
            wrapper.style.marginRight = 'auto';
        }
        break;
      case "left":
        if (blockParent) blockParent.style.textAlign = 'left';
        wrapper.style.float = "left";
        wrapper.style.marginRight = "1rem";
        break;
      case "right":
        if (blockParent) blockParent.style.textAlign = 'right';
        wrapper.style.float = "right";
        wrapper.style.marginLeft = "1rem";
        break;
      default:
        if (blockParent) blockParent.style.textAlign = '';
        break;
    }
    updateContent();
  }, [selectedImage, updateContent]);

  const deleteSelectedElement = useCallback(() => {
    if (selectedImage) {
      selectedImage.parentElement.remove();
      setSelectedImage(null);
    }
    if (selectedTable) {
      selectedTable.remove();
      setSelectedTable(null);
    }
    setSelectionCoords(null);
    updateContent();
  }, [selectedImage, selectedTable, updateContent]);

  const handleImageResize = useCallback((e) => {
    e.preventDefault();
    const startX = e.clientX;
    const startWidth = selectedImage.offsetWidth;

    const doDrag = (moveEvent) => {
      const newWidth = startWidth + (moveEvent.clientX - startX);
      if (newWidth > 40) { // Minimum width
        selectedImage.style.width = `${newWidth}px`;
        selectedImage.style.height = "auto";
        // Update overlay in real-time
        const rect = selectedImage.getBoundingClientRect();
        const editorRect = editorRef.current.getBoundingClientRect();
        setSelectionCoords({
            top: rect.top - editorRect.top + editorRef.current.scrollTop,
            left: rect.left - editorRect.left + editorRef.current.scrollLeft,
            width: rect.width,
            height: rect.height,
        });
      }
    };

    const stopDrag = () => {
      document.removeEventListener("mousemove", doDrag);
      document.removeEventListener("mouseup", stopDrag);
      updateContent();
    };

    document.addEventListener("mousemove", doDrag);
    document.addEventListener("mouseup", stopDrag);
  }, [selectedImage, updateContent]);

  // --- AI LOGIC ---

  const generateDiffHtml = (original, suggestion) => {
    const dmp = new diff_match_patch();
    const diff = dmp.diff_main(original, suggestion);
    dmp.diff_cleanupSemantic(diff);
    let html = "";
    for (const [op, data] of diff) {
      const text = data
        .replace(/&/g, "&")
        .replace(/</g, "<")
        .replace(/>/g, ">")
        .replace(/\n/g, "¶<br>");
      switch (op) {
        case DIFF_INSERT:
          html += `<ins style="background-color: #d4edda; text-decoration: none;">${text}</ins>`;
          break;
        case DIFF_DELETE:
          html += `<del style="background-color: #f8d7da; text-decoration: none;">${text}</del>`;
          break;
        case DIFF_EQUAL:
          html += `<span>${text}</span>`;
          break;
      }
    }
    return html;
  };

  const handleAiRefactor = async () => {
    if (!content.trim() || isGenerating) return;

    setIsGenerating(true);
    setAiSuggestion("");
    setIsAiLoadingMinimized(false);

    const loadingMessages = ["Analyzing...", "Generating suggestions...", "Optimizing flow...", "Finalizing..."];
    let messageIndex = 0;
    setAiLoadingStatus(loadingMessages[messageIndex]);
    const interval = setInterval(() => {
      messageIndex = (messageIndex + 1) % loadingMessages.length;
      setAiLoadingStatus(loadingMessages[messageIndex]);
    }, 2000);

    const prompt = `
<TASK>
You are a world-class content editor and a senior SEO strategist with elite proficiency in **English, Japanese, Hindi, French, Spanish, Sinhalese,Arabic and Chinese **. Your task is to reformat, stylish, and enhance the provided HTML content.
</TASK>

<LANGUAGE_PROTOCOL>
1. **Identify the Language:** Your first step is to determine if the content's primary language is one of the following:**English, Japanese, Hindi, French, Spanish, Sinhalese,Arabic and Chinese **.
  * **CRITICAL CLARIFICATION:** To do this, analyze **only the human-readable text**. You **must ignore all HTML tags** (e.g., \`<p>\`, \`<div>\`) during this language identification step.
2. **Lock the Language:** All subsequent tasks **must** be performed exclusively in the single language you have identified.
3. **No Translation:** You **must not** translate the content.
</LANGUAGE_PROTOCOL>

<INSTRUCTIONS>
1. **IMPROVE READABILITY & PROFESSIONALISM (in the identified language):**
  * Correct all grammar, spelling, and punctuation errors.
  * **Style tags appropriately using inline CSS. Ensure a clear visual hierarchy (e.g., "<h1 style="font-size: 2em;">", "<h2 style="font-size: 1.5em;">", "<p style="font-size: 1em;">").**
  * Add proper spaces and line heigh and font height for text
  * Maintain a professional and authoritative yet accessible tone.

2. **ENHANCE SEO & STRUCTURE (in the identified language):**
  * Ensure there is one single, compelling \`<h1>\` tag that captures the main topic.
  * Structure the content logically using \`<h2>\` for main sections and \`<h3>\` for sub-sections.
  * Where appropriate, convert lists or series of items into bulleted (\`<ul>\`) or numbered (\`<ol>\`) lists for better scannability.
  * Use \`<strong>\` for important keywords or phrases instead of \`<b>\`. Use \`<em>\` for emphasis instead of \`<i>\`.

3. **CORE CONTENT REQUIREMENTS:**
  * Preserve the original core message, facts, and meaning.
  * Use **inline CSS** for any styling (e.g., \`<p style="color: #333;">\`).
</INSTRUCTIONS>

<ABSOLUTE_OUTPUT_FORMAT>
-  **CRITICAL:** Your entire response must be **RAW HTML code ONLY**.
-  **DO NOT** under any circumstances wrap your response in markdown fences like \`\`\`html or \`\`\`.
-  **DO NOT** include any commentary, notes, or explanations before or after the code.
-  **DO NOT** include \`<html>\`, \`<head>\`, \`<body>\`, \`<title>\`, \`<meta>\`, or \`<script>\` tags.
-  Your response must start directly with the first HTML tag (e.g., \`<h1>\`) and end with the last closing tag.
</ABSOLUTE_OUTPUT_FORMAT>

<CONTENT_TO_IMPROVE>
${content}
</CONTENT_TO_IMPROVE>
`;

    try {
      const payload = { contents: [{ role: "user", parts: [{ text: prompt }] }] };
      const apiUrl = `https://generativelanguage.googleapis.com/v1beta/models/gemini-2.0-flash:generateContent?key=${geminiApiKey}`;
      const response = await fetch(apiUrl, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });
      if (!response.ok) throw new Error(`API call failed: ${response.status}`);
      const result = await response.json();
      const improvedHtml = result.candidates?.[0]?.content?.parts?.[0]?.text;

      if (improvedHtml) {
        setAiSuggestion(improvedHtml);
             setHighlightedDiff(generateDiffHtml(content, improvedHtml));
        setIsAiModalOpen(true);
      } else {
        throw new Error("Invalid AI response format");
      }
    } catch (error) {
      console.error("Error calling Gemini API:", error);
      setHighlightedDiff(`<p>Sorry, an error occurred. ${error.message}</p>`);
      setIsAiModalOpen(true);
    } finally {
      clearInterval(interval);
      setIsGenerating(false);
      setAiLoadingStatus("");
    }
  };

  const handleApplyAiSuggestion = () => {
    setContent(aiSuggestion);
    setIsAiModalOpen(false);
    setAiSuggestion("");
    setHighlightedDiff("");
  };

  // --- RENDER LOGIC ---

  const containerClasses = [
    "forma-text-editor-container",
    parentEditorClass,
    isFullScreen ? "forma-text-fullscreen" : "",
  ].filter(Boolean).join(" ");

  return (
    <>
      <FormaTextStyles />
      <div id={editorId} className={containerClasses} data-theme={theme}>
        <div className={`forma-text-toolbar-container ${toolBarClass}`}>
          <Toolbar
            theme={theme}
            applyStyle={applyStyle}
            applyLineHeight={applyLineHeight}
            activeStyles={activeStyles}
            onOpenLinkModal={handleOpenLinkModal}
            onImageUpload={handleImageUpload}
            onDocxUpload={handleDocxUpload}
            onVideoModalOpen={() => { saveSelection(); setIsVideoModalOpen(true); }}
            onTableModalOpen={() => { saveSelection(); setIsTableModalOpen(true); }}
            onAiRefactor={handleAiRefactor}
            content={content}
            isGenerating={isGenerating}
            isAiLoadingMinimized={isAiLoadingMinimized}
          />
        </div>

        <div className="forma-text-editor-area" onClick={handleEditorClick} onBlur={handleEditorBlur}>
          <Editor
            editorRef={editorRef}
            content={content}
            showSource={showSource}
            onContentChange={handleEditorContentChange}
            onKeyDown={handleKeyDown}
            textContentClass={`forma-text-editor-content ${textContentClass}`}
          />
          
          {/* Unified Selection Overlay and Toolbar */}
          {selectionCoords && !showSource && (
            <div
              className="forma-text-selection-overlay"
              style={{
                top: `${selectionCoords.top}px`,
                left: `${selectionCoords.left}px`,
                width: `${selectionCoords.width}px`,
                height: `${selectionCoords.height}px`,
              }}
            >
              <div className="forma-text-context-toolbar">
                {selectedImage && (
                  <>
                    <button title="Align Left" onClick={() => alignImage("left")}><AlignStartVertical size={16} /></button>
                    <button title="Align Center" onClick={() => alignImage("center")}><AlignCenterVertical size={16} /></button>
                    <button title="Align Right" onClick={() => alignImage("right")}><AlignEndVertical size={16} /></button>
                  </>
                )}
                <button title="Delete" className="forma-text-delete-btn rounded-5" onClick={deleteSelectedElement}>
                  <Trash2 size={16} />
                </button>
              </div>

              {selectedImage && (
                <div
                  className="forma-text-resize-handle "
                  onMouseDown={handleImageResize}
                ></div>
              )}
            </div>
          )}
        </div>

        <StatusBar
          theme={theme}
          wordCount={wordCount.words}
          charCount={wordCount.chars}
          onToggleSource={() => setShowSource(!showSource)}
          showSource={showSource}
          onToggleFullScreen={() => setIsFullScreen(!isFullScreen)}
          isFullScreen={isFullScreen}
        />

        {/* Modals and Popups */}
        {isGenerating && (
          <AiLoadingPopup
            status={aiLoadingStatus}
            isMinimized={isAiLoadingMinimized}
            onMinimizeToggle={() => setIsAiLoadingMinimized(!isAiLoadingMinimized)}
          />
        )}
        <LinkModal
          isOpen={isLinkModalOpen}
          onClose={() => setIsLinkModalOpen(false)}
          onApply={handleApplyLink}
          initialData={linkModalData}
          isEditing={!!editingLink}
        />
        <VideoModal
          isOpen={isVideoModalOpen}
          onClose={() => setIsVideoModalOpen(false)}
          onInsert={handleInsertVideo}
        />
        <TableModal
          isOpen={isTableModalOpen}
          onClose={() => setIsTableModalOpen(false)}
          onCreate={handleCreateTable}
        />
        <AiSuggestionModal
          isOpen={isAiModalOpen}
          onClose={() => setIsAiModalOpen(false)}
          onApply={handleApplyAiSuggestion}
          originalContent={content}
          suggestion={aiSuggestion}
          highlightedDiff={highlightedDiff}
          // diffViewClass="forma-text-diff-view"
        />
      </div>
    </>
  );
}

FormaText.propTypes = {
  content: PropTypes.string,
  setContent: PropTypes.func,
  geminiApiKey: PropTypes.string,
  parentEditorClass: PropTypes.string,
  toolBarClass: PropTypes.string,
  textContentClass: PropTypes.string,
  theme: PropTypes.string,
};
