
/**
 * Injects all necessary CSS styles for the FormaText editor.
 * All class names are prefixed with `forma-text-` to prevent conflicts.
 */
const FormaTextStyles = () => (
  <style>
    {`
      /* --- Main Container & Layout --- */
            .forma-text-source-view {
              min-height: 60vh !important;
              max-height: 75vh;
              resize: none;
              display: block;
              width: 98%;
              height: 100%;
            margin: auto;
              font-family: monospace;
              font-size: 0.875em;
              color: #f8f9fa;
              background-color: #212529;
              border: none;
              border-radius: 0;
              outline: none;
                   white-space: pre-wrap;
              word-wrap: break-word;
            overflow-x: hidden;
            }

      /* --- Main Container & Layout --- */
      .forma-text-editor-container {
        display: flex;
        flex-direction: column;
        border: 1px solid #dee2e6;
        border-radius: 0.375rem;
        box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
        background-color: #ffffff;
        max-height: 90vh;
        overflow: hidden !important; /* Prevents children from breaking border radius */
      }
      .forma-text-fullscreen {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh !important;
                max-height: 100vh !important;
        z-index: 1050;
        border-radius: 0;
        border: none;
      }
      .forma-text-toolbar-container {
        flex-shrink: 0;
        border-bottom: 1px solid #dee2e6;
        background-color: #f8f9fa;
      }
      .forma-text-editor-area {
        flex-grow: 1;
        overflow: auto;
        position: relative;
        padding: 15px;
      }
      .forma-text-editor-content:focus {
        outline: none;
        box-shadow: none;
      }

      /* --- Content Styling (Blockquote, Code) --- */
      .forma-text-editor-content blockquote {
        border-left: 4px solid #ccc;
        margin-left: 0;
        padding-left: 1rem;
        color: #6c757d;
      }
      .forma-text-editor-content pre {
        background-color: #f8f9fa;
        border: 1px solid #dee2e6;
        padding: 1rem;
        border-radius: 0.25rem;
        white-space: pre-wrap;
        word-break: break-all;
      }

      /* --- Image, Video, Table Specific Styles --- */
      .forma-text-image-wrapper {
        position: relative;
        display: inline-block;
        line-height: 0; /* Removes extra space below image */
      }
      .forma-text-image-wrapper img {
        max-width: 100%;
        height: auto;
      }
      .forma-text-video-wrapper {
        position: relative;
        padding-bottom: 56.25%; /* 16:9 aspect ratio */
        height: 0;
        overflow: hidden;
        max-width: 100%;
        background: #000;
        margin: 1rem 0;
      }
      .forma-text-video-wrapper iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
      .forma-text-table {
        width: 90%;
        border-collapse: collapse;
        border: 1px solid #a0a0a0;
        margin: 1rem auto;
      }
      .forma-text-table td {
        border: 1px solid #afafaf;
        padding: 0.75rem;
        min-width: 40px;
      }

      /* --- Selection Overlays & Contextual Toolbars --- */
      .forma-text-selection-overlay {
        position: absolute;
        pointer-events: none;
        margin-top: 13px;
        margin-left: 13px;
        border: 2px solid #0d6efd;
        z-index: 10;
        border-radius: 4px;
      }
      .forma-text-context-toolbar {
        position: absolute;
        bottom: -20px;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        gap: 4px;
        padding: 15px;
        border-radius: 6px;
        pointer-events: auto;
        z-index: 11;
      }
      .forma-text-context-toolbar button {
        background-color: #495057;
        color: white;
        border: none;
        border-radius: 4px;
        padding: 6px;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: background-color 0.2s;
      }
      .forma-text-context-toolbar button:hover {
        background-color: #6c757d;
      }
      .forma-text-context-toolbar button.forma-text-delete-btn {
        background-color: #dc3545;
      }
      .forma-text-context-toolbar button.forma-text-delete-btn:hover {
        background-color: #c82333;
      }

      /* --- Image Resize Handle --- */
      .forma-text-resize-handle {
        position: absolute;
        bottom: -7px;
        right: -7px;
        width: 14px;
        height: 14px;
        background-color: #0d6efd;
        border: 2px solid white;
        border-radius: 50%;
        cursor: se-resize;
        pointer-events: auto;
        z-index: 12;
      }

      /* --- AI Related Popups & Modals --- */
      .ai-loading-popup {
        position: fixed;
        bottom: 20px;
        right: 20px;
        z-index: 1100;
        width: 350px;
        background-color: white;
        border-radius: 0.5rem;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
        padding: 1rem;
      }
      .ai-preview-pane {
        height: 400px;
        overflow-y: auto;
        border: 1px solid #dee2e6;
        padding: 1rem;
        border-radius: 0.25rem;
        background-color: #fff;
      }
      .forma-text-diff-view ins {
        background-color: #d4edda;
        text-decoration: none;
      }
      .forma-text-diff-view del {
        background-color: #f8d7da;
        text-decoration: line-through;
        color: #721c24;
      }
      .forma-text-diff-view span {
        white-space: pre-wrap;
        word-break: break-word;
      }
      .forma-text-diff-view br[data-original-break] {
        display: none;
      }

      /* --- Modal & Button Overrides --- */
      .modal .editor-primary {
        background-color: #4285f4 !important;
        color: white !important;
        font-size: 14px;
        border-radius: 0px;
      }
      .modal .editor-secondary {
        border: 1px solid #252525ff !important;
        color: #252525ff !important;
        font-size: 14px;
        border-radius: 0px;
      }
    `}
  </style>
);

export default FormaTextStyles;