const ModernToolButtonStyles = () => (
  <style>
    {`
.forma-text-tooltip-container {
  position: relative;
  display: inline-block;
}
  
.forma-text-tooltip {
  visibility: hidden;
  opacity: 0;
  width: max-content;
  background-color: #63b1ffff;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 6px 12px;
  font-size: 12px;
  font-weight: 500;
  position: absolute;
  z-index: 10;
  top: 130%; /* Changed from 'bottom' */
  left: 50%;
  transform: translateX(-50%);
  transition: opacity 0.2s ease-in-out;
}

.forma-text-tooltip::after {
  content: "";
  position: absolute;
  bottom: 100%; 
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent #63b1ffff transparent;
}

.forma-text-tooltip-container:hover .forma-text-tooltip {
  visibility: visible;
  opacity: 1;
}


`}
  </style>
);
export default ModernToolButtonStyles;
