// src/bootstrap-injector.js

/**
 * Fetches Bootstrap CSS, scopes it to a given ID, and injects it into the head.
 * This version uses a recursive approach to correctly handle nested @-rules like @media.
 * @param {string} scopeId The unique ID of the editor's wrapper element.
 */
export function injectBootstrap(scopeId) {
  if (typeof document === "undefined") {
    return;
  }

  const cssStyleId = `bootstrap-css-scoped-${scopeId}`;
  const jsScriptId = "bootstrap-js-cdn-script";

  // --- Exit if styles are already injected ---
  if (document.getElementById(cssStyleId)) {
    return;
  }

  // --- Fetch and Scope Bootstrap CSS ---
  fetch(
    "https://cdn.jsdelivr.net/npm/bootstrap@5.3.3/dist/css/bootstrap.min.css"
  )
    .then((response) => {
      if (!response.ok) {
        throw new Error("Network response was not ok fetching Bootstrap CSS.");
      }
      return response.text();
    })
    .then((css) => {
      const scopePrefix = `#${scopeId}`;

      /**
       * Recursively scopes CSS text.
       * @param {string} cssText The CSS string to process.
       * @returns {string} The scoped CSS string.
       */
      const scopeCss = (cssText) => {
        // This regex splits the CSS into rule blocks. A rule block is a selector
        // part and the corresponding style block in braces.
        const regex = /([^{}]*)({[^{}]*})/g;

        return cssText.replace(regex, (match, selectorBlock, styleBlock) => {
          // Ignore empty selector blocks
          if (selectorBlock.trim() === "") {
            return match;
          }

          // If the selector is an @-rule (e.g., @media, @keyframes)
          if (selectorBlock.trim().startsWith("@")) {
            // Recursively scope the CSS inside the @-rule's body
            const scopedBody = scopeCss(
              styleBlock.substring(1, styleBlock.length - 1)
            );
            return `${selectorBlock}{${scopedBody}}`;
          }

          // For standard selectors, prepend the scope prefix
          const scopedSelectors = selectorBlock
            .split(",")
            .map((selector) => {
              const trimmedSelector = selector.trim();
              if (trimmedSelector) {
                // Handle special cases for root-level elements
                if (
                  trimmedSelector === "body" ||
                  trimmedSelector === "html" ||
                  trimmedSelector === ":root"
                ) {
                  return `${scopePrefix}${trimmedSelector.replace(
                    /body|html|:root/,
                    ""
                  )}`;
                }
                // Don't scope keyframe percentages
                if (
                  trimmedSelector.match(/^\d{1,2}%$/) ||
                  trimmedSelector === "from" ||
                  trimmedSelector === "to"
                ) {
                  return trimmedSelector;
                }
                return `${scopePrefix} ${trimmedSelector}`;
              }
              return ""; // Should not happen with trimmed selectors
            })
            .join(", ");

          return `${scopedSelectors} ${styleBlock}`;
        });
      };

      const finalScopedCss = scopeCss(css);

      // --- Inject the Scoped CSS into the <head> ---
      const style = document.createElement("style");
      style.id = cssStyleId;
      style.textContent = finalScopedCss;
      document.head.appendChild(style);
    })
    .catch((error) =>
      console.error("Failed to fetch and scope Bootstrap CSS:", error)
    );

  // --- Inject Bootstrap JS (does not need scoping) ---
  if (!document.getElementById(jsScriptId)) {
    const script = document.createElement("script");
    script.id = jsScriptId;
    script.src =
      "https://cdn.jsdelivr.net/npm/bootstrap@5.3.3/dist/js/bootstrap.bundle.min.js";
    script.crossOrigin = "anonymous";
    script.defer = true;
    document.body.appendChild(script);
  }
}
